<template>
  <div class="row" v-if="!currentArticle">

    <div  class="card article-item col-md-4 mb-3 no-border" 
    v-for="(article, index) in articles"  :key="index" >
      <img class="card-img-top" src="image.png" alt="Card image">
      <div class="card-body">
        <h4 class="card-title" @click="showDetail(article, index)">{{ article.title }}</h4>
        <p class="card-text">{{ article.short_description }}</p>
      </div>
    </div>
  </div>
  <div class="container row p-md-3" v-if="currentArticle">
    <div class="col-12"  >
      <h1>{{currentArticle.title}}</h1>
      <p>{{currentArticle.short_description}}</p>
      <img src="image-home.png" class="img img-fluid" >
      <div v-html="currentArticle.description"></div>
    </div>
    <div class="col-12 text-right">
      <button class="btn btn-outline-warning mb-0 ml-auto" @click="ShowAllArticle">Kembali</button>
    </div>
  </div>

</template>

<script>
import ArticleDataService from "../services/ArticleDataService";

export default {
  name: "articles-list",
  data() {
    return {
      articles: [],
      currentArticle: null,
      currentIndex: -1,
      title: ""
    };
  },
  methods: {
    retrieveArticles() {
      ArticleDataService.getAll()
        .then(response => {
          this.articles = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },
    refreshList() {
      this.retrieveArticles();
      this.currentArticle = null;
      this.currentIndex = -1;
    },
    showDetail(article, index) {
      this.currentArticle = article;
      this.currentIndex = article ? index : -1;
    },
    ShowAllArticle(){
      this.currentArticle = null;
    }

    
  },
  mounted() {
    this.retrieveArticles();
  }
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>
